'use client'
import { useState } from 'react';
import Select from './select';
import { useTexts } from '../context/Texts';
import Image from "next/image";

interface FormData {
  id: string;
  phone: string;
}

const initialState: FormData = {
  id: '',
  phone: '',
};

const options = [
  { value: "dni", label: "DNI" },
  { value: "carnet", label: "CE" },
  { value: "pasaporte", label: "Pasaporte" },
];

const ContactForm: React.FC = () => {
  const [formData, setFormData] = useState(initialState);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [isSubmitted, setIsSubmitted] = useState(false);
  const {t} = useTexts();

  const handleSelectChange = (value: string) => {
    console.log("Selected:", value);
  };

  const handleChange = (event: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = event.target;
    setFormData((prevFormData) => ({ ...prevFormData, [name]: value }));
  };

  const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    setIsSubmitting(true);
    setTimeout(() => {
      setIsSubmitting(false);
      setIsSubmitted(true);
    }, 2000);
  };

  return (
    <div className="max-w-md mx-auto p-4 bg-white rounded-lg shadow-md">
      <Image
            src="/Family.svg"
            alt="Next.js logo"
            width={136}
            height={160}
          />
      <p className='bg-gradient-to-r from-teal-400 to-yellow-200'>{t.form.caption}</p>
      <h2 className="text-lg font-bold mb-4">{t.form.title}</h2>
      <form onSubmit={handleSubmit}>
        <h3 className='mb-4'>{t.form.subtitle}</h3>
        <div>
          <div className="mb-4 grid grid-cols-2 divide-x border border-gray-300 rounded-lg">
            <Select options={options} onChange={handleSelectChange}></Select>
            <input
              className='w-full p-2 rounded-r-lg'
              type="number"
              id="id"
              name="id"
              placeholder='N. de identificacion'
              value={formData.id}
              onChange={handleChange}
            />
          </div>
        </div>
        <div className="mb-4">
          <input
            className="w-full p-2 border border-gray-300 rounded-lg focus:outline-none focus:ring focus:border-blue-500"
            type="number"
            id="phone"
            name="phone"
            placeholder='Celular'
            value={formData.phone}
            onChange={handleChange}
          />
        </div>
        <div className="flex flex-col">
          <div className='flex'>
            <input type='checkbox' />
            <p className="text-gray-600">{t.form.privacy}</p>
          </div>
          <div className='flex'>
            <input type='checkbox' />
            <p className="text-gray-600">{t.form.trades}</p>
          </div>
          <a href="#" className=" underline">{t.form.terms}</a>
        </div>
        <button
          className={`w-full p-2 bg-black text-white rounded-lg focus:outline-none focus:ring focus:border-blue-500 ${isSubmitting ? 'opacity-50 cursor-not-allowed' : ''}`}
          type="submit"
          disabled={isSubmitting}
        >
          {isSubmitting ? t.form.submitted : t.form.submit}
        </button>
        {isSubmitted && (
          <p className="text-green-500 mt-4">{t.form.message}</p>
        )}
      </form>
    </div>
  );
};

export default ContactForm;
